<template>
    <el-dialog :title="titleShow==true?'add':'edit'"  v-model="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="PackageUid:" :label-width="formLabelWidth" v-if="titleShow==true">
                <el-input v-model.trim="uid" auto-complete="off" size="small" disabled></el-input>
            </el-form-item>
            <el-form-item label="CompanyCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.CompanyCode" auto-complete="off" size="small"></el-input>
            </el-form-item>
            <el-form-item label="PackageName:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.PackageName" auto-complete="off" size="medium"></el-input>
            </el-form-item>
            <el-form-item label="StartDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleDate"
                        type="date"
                        placeholder="Select StartDate">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="EndDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model.trim="singleDate1"
                        type="date"
                        placeholder="Select EndDate">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="EmpCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="EmpCode" auto-complete="off" disabled></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">Cancel</el-button>
            <el-button type="primary" @click="submit()">Submit</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
.updatePwd{
    .el-dialog__header{
        padding:30px;
        .el-dialog__headerbtn{
            width:50px;
            height:50px;
            margin:-30px -30px 0 0;
        }
    }
    .el-dialog__body{
        padding:30px 50px 0 0;
    }
}
</style>

<script>
import QuRequest from '../../../core/QuRequest'

const quRequest = new QuRequest()

export default {

    data() {
        return {
            titleShow:false,
            dialogForm: false,
            formLabelWidth: '150px',
            disabled:true,
            isShow:true,
            EmpCode:'',
            uid:'',
            form:{
                'CompanyCode':'',
                'PackageName':'',
                'StartDate':'',
                'EndDate':'',
            }
        }
    },
    computed:{
        singleDate: {
            set (newValue) {
                if(newValue){
                    this.form.StartDate = this.formatDate(newValue)
                }else{
                    this.form.StartDate = '';
                }
            },
            get () {
                return this.form.StartDate
            }
        },
        singleDate1: {
            set (newValue) {
                if(newValue){
                    this.form.EndDate = this.formatDate(newValue)
                }else{
                    this.form.EndDate = '';
                }
            },
            get () {
                return this.form.EndDate
            }
        },
    },
    mounted() {
        this.getUid();
    },
    methods: {
        getUid(){
            quRequest.send(quRequest.Apis.qutuiba.member.uid
            ).then(res => {
                this.uid = res.result[0];
            }).catch(err => {
                this.$message.error(`Failed to retrieve data：${ err.message || 'Unknown error' }`);
            }).fin(() => {
                this.loading = false;
            }).done();
        },
        formatDate(dateStr){
            let date = new Date(dateStr);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            let d = date.getDate();
            return [y, this.addZero(m), this.addZero(d)].join('/');
        },
        addZero: function (num) {
            return (num > 9 ? '' : '0') + num
        },
        showInfoDialog (empcode,item) {
            if(arguments.length>1){
                this.isShow = false;
                this.form.CompanyCode =item.CompanyCode;
                this.form.PackageName=item.PackageName;
                this.form.StartDate= item.StartDate;
                this.form.EndDate= item.EndDate;
                this.titleShow = false;
            }else{
                this.titleShow = true;
                this.form.CompanyCode ='';
                this.form.PackageName='';
                this.form.StartDate= '';
                this.form.EndDate= '';
            }
            this.EmpCode = empcode;
            this.dialogForm = true;
        },
        submit(){
            quRequest.send(quRequest.Apis.qutuiba.member.update,this.titleShow==true?{'json' :'{update:{EmployeePackage:['+JSON.stringify(Object.assign({'EmpCode':this.EmpCode,'PackageUid':this.uid},this.form))+']}}'}:{'json' :'{update:{EmployeePackage:['+JSON.stringify(Object.assign({'EmpCode':this.EmpCode},this.form))+']}}'}
            ).then(res => {
                this.$message.success("Submit Success！");
                this.dialogForm = false;
                this.$emit('update')
            }).catch(err => {
                this.$message.error(err.message);
            }).fin(() => {
                this.loading = false;
            }).done();
        }
    }
}
</script>


